<template>
  <v-app id="app">
    <Navbar :key="$route.fullPath" />

    <v-content class="my-container">
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>

    <v-footer absolute class="font-weight-medium" app>
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }} -
        <strong>Rent App</strong>
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
import Navbar from "@/components/Navbar";
export default {
  name: "app",
  components: {
    Navbar
  }
};
</script>

<style>
main,
header,
footer {
  font-family: "Exo 2" !important;
}

.icon-padding-right {
  padding-right: 10px;
}

.my-container {
  margin-left: 10%;
  margin-right: 10%;
}
</style>



