<template>
  <v-form>
    <v-card-text>
      <v-container>
        <v-text-field
          type="email"
          v-model="email"
          label="Email"
          color="teal"
          autofocus
          :error-messages="emailErrors"
          @input="$v.email.$touch()"
          @blur="$v.email.$touch()"
        ></v-text-field>
        <v-text-field
          type="password"
          autocomplete="password"
          v-model="password"
          label="Password"
          color="teal"
          :error-messages="passwordErrors"
          @input="$v.password.$touch()"
          @blur="$v.password.$touch()"
        ></v-text-field>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-container>
        <v-btn
          @click="login"
          :loading="isLoading"
          :disabled="isLoading"
          color="teal"
          class="ma-2 white--text"
          type="submit"
        >
          Login
          <v-icon right dark>fa fa-sign-in-alt</v-icon>
        </v-btn>
      </v-container>
    </v-card-actions>
  </v-form>
</template>

<script>
import firebase from "firebase";
import db from "./firebaseInit";

import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  name: "Luca",
  mixins: [validationMixin],
  data() {
    return {
      email: "",
      password: "",
      isLoading: false
    };
  },
  methods: {
    login(e) {
      e.preventDefault();
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.isLoading = true;
        firebase
          .auth()
          .signInWithEmailAndPassword(this.email, this.password)
          .then(
            user => {
              this.isLoading = false;
              this.$router.push("/dashboard");
            },
            err => {
              this.isLoading = false;
              alert(err.message);
            }
          )
          .catch(err => {
            this.isLoading = false;
            alert(err);
          });
      }
    }
  },
  validations: {
    email: { required },
    password: { required }
  },
  computed: {
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.required && errors.push("Email is required");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push("Password is required");
      return errors;
    }
  }
};
</script>

<style></style>



