<template>
  <div>
    <v-navigation-drawer v-model="sideNav" temporary app>
      <v-list-item>
        <v-list-item-avatar>
          <v-icon>fa fa-user</v-icon>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{ currentUser }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-navigation-drawer>

    <v-app-bar color="teal" dark app>
      <v-app-bar-nav-icon
        @click="sideNav = !sideNav"
        class="d-flex d-sm-none"
      ></v-app-bar-nav-icon>

      <v-toolbar-title>
        <v-icon style="vertical-align: unset;">fa fa-house-user</v-icon>
        <span style="font-size: 1.5rem;">Rentii</span>
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-toolbar-items class="d-none d-sm-flex">
        <v-btn text @click="go('/')">Home</v-btn>

        <v-btn v-if="isLoggedIn" text @click="go('/dashboard')"
          >Dashboard</v-btn
        >
        <v-btn v-if="isLoggedIn" text @click="logout">Logout</v-btn>
        <v-btn v-if="!isLoggedIn" text @click="go('/login')">Login</v-btn>
      </v-toolbar-items>
    </v-app-bar>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "navbar",
  data() {
    return {
      sideNav: false,
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    go(route) {
      this.$router.push(route);
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: "/" });
        });
    }
  }
};
</script>

<style scoped>
.icon-padding-right {
  padding-right: 10px;
}
</style>



