<template>
  <div>
    <v-container>
      <v-card>
        <v-tabs v-model="tab" background-color="teal" dark>
          <v-tab href="#login">Login</v-tab>
          <v-tab href="#register">Register</v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item value="register">
            <Register />
          </v-tab-item>

          <v-tab-item value="login">
            <Login />
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import Register from "./Register";
import Login from "./Login";

export default {
  name: "login-register-form",
  components: {
    Register,
    Login
  },
  data() {
    return {
      tab: null
    };
  }
};
</script>

<style></style>



